

function sleep(ms) {
    return new Promise((resolve)=> {
        setTimeout(resolve, ms)
    })
}

async function worker(task, callback) {
    try {
        if (task.delay) await sleep(task.delay);

        const adsInfo = await parseFrontPage(task.pageNumber);

        console.log('@worker finished task successfully, dunno what to do', adsInfo);
        callback()
    } catch (error) {
        callback(error)
    }
}